.no-webp .inicio {
    background-image: url(../img/header.jpg);
}
.webp .inicio {
    background-image: url(../img/header.webp);
}

.header {
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;
    min-height: 40rem;

    &.inicio {
        background-position: center center;
        background-size: cover;

        @include tablet {
            min-height: 40rem;
        }

        .contenido-header {
            @include tablet {
                min-height: unset;
            }
        }
    }
}

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1, h2 {
        text-align: center;
        color: $blanco;
        font-weight: $bold;
        line-height: 20px;
        max-width: 100%;
    }

    h2{
        margin-top: 8rem;
    }
}